import $ from 'jquery';

import {
    itemsObject,
    getDOMElements,
    isEmpty,
    isDefined,
    filterMeta,
    filterMetaValue
}                     from './Utilities.js';
import UserSelector   from './UserSelector.js';
import MetaboxItems   from './MetaboxItems.js';
import MetaboxFooter  from './MetaboxFooter.js';
import ItemCreatorAdd from './ItemCreatorAdd.js';
import Closures       from './Closures.js';

class Actions {

    /**
     * Renders all the actions in the UI
     * @class
     *
     */

    constructor(){
        console.log(UncannyAutomator)
        // Debugging: Create console group
        // console.group( 'Actions' );

        // Get and clear metabox
        let $metabox = getDOMElements( 'metabox', { type: 'action' });
        $metabox.empty();

        // Check if we have to create the User Selector
        if ( UncannyAutomator.recipe.type == 'anonymous' && isDefined( UncannyAutomator.wp ) && UncannyAutomator.wp ){
            // Create User Selector
            new UserSelector();
        }

        // Get recipe actions
        let actions = itemsObject( 'get', {
            filter: {
                itemType: 'action',
            }
        });

        let hasClosures = false;
        let closuresLabel =  UncannyAutomator.i18n.action.asyncActions.closuresLabelInstant;

        // Check if it has actions
        if ( ! isEmpty( actions ) && isDefined( UncannyAutomator.wp ) && UncannyAutomator.wp ){

            let instantActions = filterMeta( actions, 'async_mode', false );

            // Render non async actions
            new MetaboxItems( 'action', instantActions );

            let delayedActions = filterMetaValue( actions, 'async_mode', 'delay' );
            let scheduledActions = filterMetaValue( actions, 'async_mode', 'schedule' );

            if ( ! isEmpty( delayedActions ) || ! isEmpty( scheduledActions ) ) {
                closuresLabel =  UncannyAutomator.i18n.action.asyncActions.closuresLabelAsync;
                // Add Closures early
                new Closures( closuresLabel );

                // Prevent closures from appearing later
                hasClosures = true;
            }

            // Render async actions
            new MetaboxItems( 'action', delayedActions );
            new MetaboxItems( 'action', scheduledActions );
  
        } else if ( ! isEmpty( actions ) ) {
            new MetaboxItems( 'action', actions );
        } else {
            // Render the first step of the item creator
            new ItemCreatorAdd( 'action' );
        }

        // Create metabox footer
        new MetaboxFooter( 'action' );

        // Debugging: Close console group
        // console.groupEnd();


        // Show closures if they were not shown earlier
        if ( ! hasClosures ) {
            new Closures( closuresLabel );
        }

    }

    
}

export default Actions;